#fade{
  
  animation: faded .2s ease-in;
}

@keyframes faded {
  from {opacity: 0}
  to {opacity: 1}
}

.scoreboard *{
  /* color:black;
  background-color:#c6a1a1; */
   animation: faded 2s ease 
}

.scoreboard h1{
  color:#d7a8a8;
  margin-bottom:1px;
  /* padding:10px; */
}

ol{
  /* padding:10px; */
  list-style: none;
  counter-reset: my-awesome-counter;
}

ol li{
  counter-increment:my-awesome-counter;
}

ol li::before{
  content: counter(my-awesome-counter) ". ";
  color: green;
  font-weight: bold;
}

ol li:nth-child(3)~li::before{
  color: white;
}