.game-area{
  background-color: whitesmoke;
  position: relative;
  /* margin: 50px auto; */
  /* margin-top: 0px; */
  width: 400px;
  height: 400px;
  border: 2px solid #000;
  z-index:0;
}
.snake-dot{
  position: absolute;;
  width: 2%;
  height: 2%;
  background-color: #000;
  border: 1px solid #fff;
}

.snake-food{
  position: absolute;
  width: 2%;
  height: 2%;
  background: red;
  border:1px solid #fff;
  z-index: 1;
  border-radius: 50%;
}

.start{
  position: absolute;
  top: 50%;
  left: 50%;
  height: 50px;
  width: 50px;
  transform: translate(-25px,-25px);
  /* box-shadow:0px 0px 20px 200px rgba(158, 23, 23, 0.533); */
  z-index:5;
  border:2px solid black;
}


.start svg{
  height:100%;
  width:100%;
  color: #351818;
  cursor:pointer;
}

.start::after{
  content:'';
  position: absolute;
  top: -177px;
  left: -177px;
  background-color: #c4767699;
  height: 400px;
  width:400px;
  z-index:-1
}

.game button{
  display: flex;
  justify-content:center;
  padding:0px;
  cursor:pointer;
}

.game-area{
  margin: auto;
}

#settings {
  display: flex;justify-content: center;
}